import { Component, OnInit } from "@angular/core";
import { FormGroup, FormControl , FormBuilder, Validators} from "@angular/forms";

@Component({
    selector: "app-signup",
    template: `
    <form (ngSubmit)="onSubmit()" [formGroup]="formSignUp">
        <input type="text" placeholder="Enter user name" name="email" formControlName="email">
        <br>
        <input type="password" placeholder="Enter password" name="password" formControlName="password">
        <br>
        <div formGroupName="subject">
            <label><input type="checkbox" formControlName="angular">Angular</label>
            <label><input type="checkbox" formControlName="react">React</label>
            <label><input type="checkbox" formControlName="vue">Vue</label>
        </div>
        <button [disabled]="formSignUp.invalid"> Đăng nhập</button>
    </form>
    `
})
export class SignUpComponent implements OnInit{
    formSignUp!: FormGroup;
    constructor(private fb : FormBuilder){
        
    }
    ngOnInit(): void {
        this.formSignUp = this.fb.group({
            email :['',Validators.required],
            password : '',
            subject: this.fb.group({
                angular: false,
                react : false,
                vue : false
            })
        });
    }

    onSubmit(){
        return console.log(this.formSignUp.value);
    }
}