import { Component, OnInit } from '@angular/core';
import { IpService } from './ip.service';

@Component({
    selector: 'app-ip',
    template: '<h1>{{ip}}</h1>',
    providers: [IpService]
})

export class IpComponent implements OnInit{
    ip="";
    constructor(private ipService : IpService) {
        
    }
    ngOnInit(): void {
        this.ipService.getIp().then(ip => this.ip = ip);
    }
     
}