import { SigninService } from './signin.service';
import {  HttpHeaders, HttpClient } from '@angular/common/http';
import { Component } from "@angular/core";

@Component({
    selector: "app-sign",
    template: `
    <form (submit)="onSubmit(formSign)" #formSign="ngForm">
        <input type="text" placeholder="Enter user name" ngModel #txtEmail="ngModel" name="email" required email>
        <p *ngIf="txtEmail.touched && txtEmail.errors?.required">Email not valid</p>
        <p *ngIf="txtEmail.errors?.email">Email not exits</p>
        <br><br>
        <input type="password" placeholder="Enter password" ngModel #txtPass="ngModel" name="password" required minlength="2" pattern=[a-z]*>
        <br>
        <label>
            <input [ngModel]="true" name="remember" type="checkbox"> Remember
        </label>
        <br>
        <button [disabled]="formSign.invalid"> Đăng nhập</button>
    </form>
        <p>{{txtEmail.errors | json}}</p>
        <p>{{txtPass.errors | json}}</p>
        <p>{{formSign.value | json}}</p>
    `,
    providers: [SigninService]
})
export class SignInComponent {
    constructor(private signinService: SigninService) {

    }
    
    onSubmit(formSign: any) {
        this.signinService.onSubmit(formSign.value).then((res:any) => console.log(res.email));
    }
}